import  axios from "axios"
import router from '@/router';


export default {
    name: "Register",
    data() {
        return {
            awaitMsg : "",
            validationsRegister: "",
            username : "",
            email: "",
            adress: "",
            phone: "",
            password: "",
            confirmPassword: "",
            userCreated: "",
            baseURL: "https://backend-turtle-g8759xzxz-tiagobonito44-hotmailcom.vercel.app/register"
        }
    }, 

    methods: {
        createUser() {
            // tentando criar um usuario
            this.awaitMsg = "Aguarde, estamos tentando criar sua conta."
            const validationsPassed = this.validations()
      
            if (!validationsPassed) {
              this.awaitMsg = ""
              return
            }
      
            const fields = {
              username: this.username,
              email: this.email,
              adress: this.adress,
              phone: this.phone,
              password: this.password,
              userCreated: ""
            }
      
            axios.post(`${this.baseURL}`, fields)
              .then((res) => {
                console.log(JSON.stringify(res.data))
                this.userCreated = "Usu√°rio criado com sucesso, divirta-se! ‚úîüíö"
                setTimeout(() => {
                  router.push({ name: "login" })
                }, 4000)
              })
              .catch((err) => {
                console.log(err)
                if (err.response && err.response.data && err.response.data.message) {
                  if (err.response.data.message === "Username already exists.") {
                    this.validationsRegister = "Apelido j√° existe, escolha outro."
                  } else if (err.response.data.message === "Email already exists.") {
                    this.validationsRegister = "E-mail j√° existe, tente outro."
                  }
                } else {
                  this.validationsRegister = "Erro ao criar usu√°rio. Por favor, tente novamente."
                }
                this.awaitMsg = ""
              })
          },
      
          validations() {
            this.validationsRegister = ""
      
            if (!this.username || !this.email || !this.adress || !this.phone || !this.password || !this.confirmPassword) {
              this.validationsRegister = "Preencha todos os campos para prosseguir"
              return false;
            }
      
            if (this.username.length >= 12) {
              this.validationsRegister = "Seu apelido s√≥ pode ter 12 d√≠gitos"
              return false;
            }
      
            if (!this.email.includes(".com")) {
              this.validationsRegister = "O e-mail informado n√£o cont√©m a parte '.com'"
              return false;
            }

      
            if (this.adress.length >= 15) {
              this.validationsRegister = "Nacionalidade excessivamente grande"
              return false;
            }
      
            if (this.password.length < 8) {
              this.validationsRegister = "Sua senha precisa ter pelo menos 8 d√≠gitos"
              return false;
            }
      
            if (this.phone.length < 8) {
              this.validationsRegister = "Seu n√∫mero precisa ter pelo menos 8 d√≠gitos"
              return false;
            }
      
            if (this.password !== this.confirmPassword) {
              this.validationsRegister = "As senhas n√£o coincidem, tente novamente"
              return false;
            }
      
            return true;
          }
        }
}